}

.restaurant-menu-cell:nth-child(2) {
  text-align: center;
}

.restaurant-menu-cell:nth-child(3) {
  text-align: right;
  font-weight: 700;
}

.item-margin {
  margin-top: 8px;
}

.restaurant-button-style {
  color: var(--accent-color);
  transition-property: background-color, boder;
  transition: var(--transition-fast);
  background: #000;
  border: 2px solid #fffdee;
  border-radius: 25px;
  margin-left: auto;
  margin-right: auto;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  display: block;
}

.restaurant-button-style:hover, .restaurant-button-style:focus, .restaurant-button-style:active {
  background-color: var(--logo-theme-dark);
  border: 2px solid var(--accent-color);
